<template>
  <v-menu
    ref="menu"
    v-model="menu"
    :close-on-content-click="false"
    :return-value.sync="date"
    transition="scale-transition"
    offset-y
    min-width="auto"
  >
    <template v-slot:activator="{ attrs }">
      <div v-bind="attrs" @click="menu=!menu" :class="classActual"><slot v-bind:value="date"></slot></div>
    </template>
    <v-date-picker
      v-if="type === 'date'"
      v-model="date"
      no-title
      scrollable
      :max="max"
      :min="min"
      @change="save"
    />
      <v-time-picker
        v-if="type === 'time'"
        v-model="date"
        no-title
        scrollable
        :max="max"
        :min="min"
        @change="save"
    />
  </v-menu>
</template>

<script>

const formatter = (date, type) => {
  if (date) {
    let parsed
    try {
      parsed = new Date(date).toISOString()
    } catch (e) { }
    if (type === 'date' && parsed) {
      return parsed.substr(0, 10)
    }
    if (type === 'time') {
      return parsed ? parsed.substr(11, 16) : date
    }
  }
  return null
}

export default {
  name: 'DataBitPicker',

  components: {
  },

  props: {
    classActual: { },
    type: { default: 'date' },
    value: { },
    def: { },
    defProp: { },
    schema: { }

  },

  data: () => ({
    menu: false
  }),

  computed: {
    date: {
      get () {
        const val = (this.value || this.def) || (this.defProp ? this[this.defProp] : null)
        return formatter(val, this.type)
      },
      set (value) {
        this.$emit('input', value)
      }
    },

    min () {
      const { schema: { min } = { } } = this
      return min
    },

    max () {
      const { schema: { max } = { } } = this
      return max
    }

  },

  methods: {
    save (date) {
      this.$refs.menu.save(date)
    }
  }
}
</script>

