<template>
  <v-data-table
    class="prj-data-table"
    :headers="headers"
    :items="items"
    v-bind="$attrs"
  >
    <template v-for="(_, slot) of $scopedSlots" v-slot:[slot]="scope"><slot :name="slot" v-bind="scope"/></template>
  </v-data-table>
</template>

<script>
export default {
  name: 'AppDataTable',

  props: {
    headers: {
      type: Array,
      default: undefined
    },
    items: {
      type: Array,
      default: undefined
    }
  }
}
</script>

<style lang="sass">
  .prj-data-table
    .v-data-table-header th span
      text-transform: uppercase
      opacity: .8

  .theme--light
    &.prj-data-table
      .v-data-table-header
        background-color: #F5F6FA

  .theme--dark
    &.prj-data-table
      .v-data-table-header
        background-color: map-get($material-dark, 'background')

</style>
